import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/pictures",
    name: "Pictures",
    component: () =>
      import(/* webpackChunkName: "pictures" */ "../views/Pictures.vue")
  },
  {
    path: "/comics",
    name: "Comics",
    component: () =>
      import(/* webpackChunkName: "comics" */ "../views/Comics.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
